@import url(https://fonts.googleapis.com/css?family=Work+Sans:400,500,600&display=swap);
/* @import url("https://fonts.googleapis.com/css?family=Montserrat:400,500,600,700&display=swap"); */

:root {
  --primary: #13171a;
  --on-primary: #ffffff;
  --secondary: #0062ff;
  --danger: #da1e28;
  --white: #ffffff;
  --black: #000000;
  --grey: #f2f4f8;
  --background-color: #000000;
  --text-color: #ffffff;
}

html {
  box-sizing: border-box;
  font-size: 16px;
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

body {
  font-family: "Work Sans", -apple-system, BlinkMacSystemFont, "Segoe UI",
    "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

/* Typography */

h1,
.h1 {
  font-size: 4.25em;
  line-height: 1.01470588em;
  margin-top: 0.33823529em;
  margin-bottom: 0.67647058em;
}
h2,
.h2 {
  font-size: 2.625em;
  line-height: 1.0952381em;
  margin-top: 0.54761905em;
  margin-bottom: 0.54761905em;
}
h3,
.h3 {
  font-size: 1.625em;
  line-height: 1.76923077em;
  margin-top: 0.88461538em;
  margin-bottom: 0em;
}
h4,
.h4 {
  font-size: 1em;
  line-height: 1.4375em;
  margin-top: 1.4375em;
  margin-bottom: 0em;
}
h5,
.h5 {
  font-size: 1em;
  line-height: 1.4375em;
  margin-top: 1.4375em;
  margin-bottom: 0em;
}
p,
ul,
ol,
pre,
table,
blockquote {
  margin-top: 0em;
  margin-bottom: 1.4375em;
}
ul ul,
ol ol,
ul ol,
ol ul {
  margin-top: 0em;
  margin-bottom: 0em;
}

/* Let's make sure all's aligned */
hr,
.hr {
  border: 1px solid;
  margin: -1px 0;
}

sub,
sup {
  position: relative;
  vertical-align: baseline;
}
sup {
  top: -0.5em;
}
sub {
  bottom: -0.25em;
}

