:root {
  --black: #010101;
  --white: #fbfbfb;
  --red: #f6033b;
  --yellow: #fcee08;
  --aqua: #3ed8f2; }

html,
body {
  margin: 0;
  height: 100%; }

a {
  color: inherit;
  text-decoration: none; }

ol,
ul {
  padding: 0; }

li {
  list-style: none; }

.container {
  display: flex;
  justify-content: center;
  flex-direction: column;
  position: relative;
  padding: 1rem 2rem;
  margin: 0 auto;
  border-radius: 3px;
  color: #fbfbfb;
  color: var(--white);
  overflow: hidden;
  -webkit-transition: all 0.3s cubic-bezier(0.25, 0.8, 0.25, 1);
  transition: all 0.3s cubic-bezier(0.25, 0.8, 0.25, 1);
  text-align: left; }

.form {
  width: 100%; }

ul.results {
  padding: 2rem;
  border: 1px solid grey;
  color: black;
  display: flex;
  flex-direction: column;
  align-items: center; }
  ul.results li {
    margin-bottom: 1rem; }
  ul.results li.result {
    padding: 2px 4px;
    background: gold; }

p {
  color: black; }

.test {
  padding: 2rem;
  border: 1px solid grey;
  display: flex;
  flex-direction: column;
  align-items: center; }

.row {
  display: flex;
  flex-direction: column;
  justify-content: center; }

.header {
  background: #fcee08;
  background: var(--yellow);
  background-size: cover;
  padding-left: 2rem;
  display: grid;
  justify-items: start;
  align-content: center; }
  .header > * {
    margin: 0; }
  @media (max-width: 590px) {
    .header {
      --span: 12;
      padding: 1rem; }
      .header h2 {
        font-size: 2rem; } }

.overflow-hidden {
  position: fixed;
  overflow: hidden;
  width: 100%; }

body::-webkit-scrollbar {
  width: 0 !important; }

body::-webkit-scrollbar {
  width: 0 !important; }

body {
  overflow: -moz-scrollbars-none;
  -webkit-overflow-scrolling: touch; }

